	module.exports = {"data":{"markdownRemark":{"html":"<hr>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/mistborn.jpg\" alt=\"alt text\" title=\"Mistborn\"></p>\n<p>Hi again! Going off of yesterday's post about <em>The Stormlight Archive</em>, today I'll be writing a review of another one of Brandon Sanderson's series, <em>Mistborn</em>. </p>\n<p>The Mistborn books are much shorter than the Stormlight Archive books (roughly 500 pages each), but there are more of them. There's actually two separate trilogies currently set in the Mistborn world, and there are more incoming.</p>\n<p>The first trilogy takes place in a world where most of the population are essentially starving slaves, called skaa, ruled over by a wealthy aristocracy, and, more imporantly, an immortal supreme emperor. Ash constantly falls from the sky, and at the start of the first novel, things are getting so bad that it is becoming difficult to survive oustide of cities.</p>\n<p>Some people in this world have the ability to use magic, but the ability is hereditary; only nobles can use it. </p>\n<p>The magic system is pretty unique: there are different types of metals that, when ingested by someone who can use them (called an allomancer), they can use the ability for that specific metal. Most allomancers can only use one metal, and therefore are given nicknames associated with the metal they can use. For example, Coinshots can use steel when ingested to push off of metal; essentially allowing them to fly by pushing off of coins. It works by basic physics: you're pushing your weight against the weight of the coin, and so to push more weight, you need more weight on the other side in return. Every metal has an opposite metal. For example, Lurchers can pull on iron, doing the opposite of Coinshots.</p>\n<p>A few rare allomancers, however, can use every metal, as long as they ingest it. That's where two of the main characters of the books come in -  Vin, a street urchin who doesn't realize that she's an allomancer, and Kelsier, a half-skaa thief who finds her and develops a bold plan: to take down the emperor himself.</p>\n<p>There are many other interesting characters in these books; mostly the thief crew itself, but also various nobles (some of whom are intergral to the plot).</p>\n<p>These books are really good, especially if you're looking for a distopian epic fantasy, with a unique magic system, that takes the traditional hero's journey story and tells it from a strong female perspective. </p>\n<p>One more thing - without giving too much away, the second trilogy takes place in the same world as Mistborn, but in more modern times, with technology equivilant to the roaring twenties in our world.</p>\n<p>That's all for today. See you next time!</p>\n<p>-- Bradley</p>","frontmatter":{"date":"August 14, 2018","path":"/mistborn","title":"Mistborn","tags":["Book Review","Fantasy"]}}},"pathContext":{}}

